.contact {
  text-align: center;
  background-color: #f0e6de;
  padding: 60px;
}

.contact_phrase {
  text-align: center;
  color: #ffffff;
  margin: 45px;
  font-size: 20px;
  text-transform: uppercase;
  letter-spacing: 5px;
  font-weight: 700;
}

.contact-form {
  margin-left: 32%;
}

.form-group {
  margin-bottom: 15px;
}

.contact-form label {
  display: flex;
  justify-content: left;
  align-items: center;
  margin-bottom: 5px;
  font-weight: bold;
  color: #333333;
  font-size: 15px;
}

.contact-form input,
.contact-form textarea {
  width: 52%;
  padding: 7px;
  border: none;
  border-radius: 5px;
  resize: vertical;
  outline: none;
  display: block;
  background-color: #faf7f4;
}
.contact-form textarea {
  padding-top: 20px;
  height: 80px;
}

.contact-form input:focus,
.contact-form textarea:focus {
  outline: 2px solid #f0e6de;
}

.contact-form button {
  width: 54%;
  padding: 7px;
  border: none;
  background-color: #d9c8b8;
  color: #333333;
  font-size: 15px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
  font-weight: bold;
  display: block;
}

.contact-form button:hover {
  background-color: #e5dcd3;
}
@media (max-width: 480px) {
  .contact_phrase {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    letter-spacing: normal;
  }
  .contact_phrase h3 {
    font-size: 14px;
  }
  .contact-form {
    margin: auto;
  }
  .contact-form input,
  .contact-form textarea {
    width: 100%;
  }
  .contact-form button {
    width: 100%;
    margin: 5px;
    font-size: 10px;
  }
  .contact-form label {
    font-size: 10px;
  }
}
